@import url("../../globalStyles/App.css");

.MoreInfo{
    display: flex;
    justify-content: center;
    align-items: center;
    align-self: center;
    margin-bottom: 60px;
    width: 100%;
}
.MoreInfo2{
    flex-direction: row-reverse;
}
.imgContainer{
    width: 50%;
    height: auto;
}
.imgContainer img{
    width: 70%;
    height: 70%;
}
.titleContainer{
    width: 50%;
    height: 100%;
    margin: 0px 20px 0px 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    color: var(--Lprimary);

}
.titleContainer h2{
    height: 20%;
    font-weight: var(--subTitle);
    font-size: calc(15px + 0.5vw);
}
.titleContainer p{
    height: 80%;
    font-weight: var(--text);
    line-height: 40px;
    text-align: left;
    font-size: calc(12px + 0.3vw);
    margin: 20px 20px 0px 20px;
}

@media (max-width:900px) {
    .MoreInfo{
        flex-direction: column;
        width: 130%;
        margin-bottom: 100px;
    }
    .MoreInfo2{
        flex-direction: column;
    }
    .imgContainer{
        width: 60%;
        margin-bottom: 20px;
    }
    .titleContainer{
        width: 70%;
    }
    .imgContainer img{
        width: 100%;
        height: 80%;
    }
}